body {
  margin: 0;
  padding: 0;
  background-color: rgba(5, 71, 71, 0.398);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#movies img,
#movie img {
  width: 100%;
}
.brand-text {
  font-size: 2rem;
}

@media (min-width: 960px) {
  #movies .col-md-3 .well {
    height: 390px;
  }

  #movies .col-md-3 img {
    height: 240px;
  }
}


.navbar input[type="checkbox"],
.navbar .hamburger-lines {
  display: none;
}

.navbar {
  box-shadow: 0px 5px 10px 0px #aaa;
  position: relative;
  width: 100%;
  background: #fff;
  color: #000;
  opacity: 0.85;
  height: 50px;
  z-index: 12;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 64px;
  align-items: center;
}

.menu-items {
  order: 2;
  display: flex;
}

.menu-items li {
  list-style: none;
  margin-left: 1.5rem;
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
}

.menu-items a {
  text-decoration: none;
  color: #444;
  font-weight: 500;
  transition: color 0.3s ease-in-out;
}

.menu-items a:hover {
  color: #117964;
  transition: color 0.3s ease-in-out;
}

.logo {
  order: 1;
  font-size: 2.3rem;
  margin-bottom: 0.5rem;
}

/* ......../ media query /.......... */

@media (max-width: 768px) {
    .navbar {
      opacity: 0.95;
    }
  
    .navbar-container input[type="checkbox"],
    .navbar-container .hamburger-lines {
      display: block;
    }
  
    .navbar-container {
      display: block;
      position: relative;
      height: 64px;
    }
  
    .navbar-container input[type="checkbox"] {
      position: absolute;
      display: block;
      height: 32px;
      width: 30px;
      top: 20px;
      left: 20px;
      z-index: 5;
      opacity: 0;
    }
  
    .navbar-container .hamburger-lines {
      display: block;
      height: 23px;
      width: 35px;
      position: absolute;
      top: 17px;
      left: 20px;
      z-index: 2;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
  
    .navbar-container .hamburger-lines .line {
      display: block;
      height: 4px;
      width: 100%;
      border-radius: 10px;
      background: #333;
    }
  
    .navbar-container .hamburger-lines .line1 {
      -webkit-transform-origin: 0% 0%;
              transform-origin: 0% 0%;
      transition: -webkit-transform 0.4s ease-in-out;
      transition: transform 0.4s ease-in-out;
      transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
    }
  
    .navbar-container .hamburger-lines .line2 {
      transition: -webkit-transform 0.2s ease-in-out;
      transition: transform 0.2s ease-in-out;
      transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
    }
  
    .navbar-container .hamburger-lines .line3 {
      -webkit-transform-origin: 0% 100%;
              transform-origin: 0% 100%;
      transition: -webkit-transform 0.4s ease-in-out;
      transition: transform 0.4s ease-in-out;
      transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
    }
  
    .navbar .menu-items {
      padding-top: 100px;
      background: #fff;
      height: 100vh;
      max-width: 300px;
      -webkit-transform: translate(-150%);
              transform: translate(-150%);
      display: flex;
      flex-direction: column;
      margin-left: -40px;
      padding-left: 50px;
      transition: -webkit-transform 0.5s ease-in-out;
      transition: transform 0.5s ease-in-out;
      transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
      box-shadow: 5px 0px 10px 0px #aaa;
    }
  
    .navbar .menu-items li {
      margin-bottom: 1.5rem;
      font-size: 1.3rem;
      font-weight: 500;
    }
  
    .logo {
      position: absolute;
      top: 5px;
      right: 15px;
      font-size: 2rem;
    }
  
    .navbar-container input[type="checkbox"]:checked ~ .menu-items {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
  
    .navbar-container input[type="checkbox"]:checked ~ .hamburger-lines .line1 {
      -webkit-transform: rotate(35deg);
              transform: rotate(35deg);
    }
  
    .navbar-container input[type="checkbox"]:checked ~ .hamburger-lines .line2 {
      -webkit-transform: scaleY(0);
              transform: scaleY(0);
    }
  
    .navbar-container input[type="checkbox"]:checked ~ .hamburger-lines .line3 {
      -webkit-transform: rotate(-35deg);
              transform: rotate(-35deg);
    }
}


  #slideshow {
	/* margin: 0 auto; */
	padding-top: 1px;
	height: 400px;
	width: 100%;
	box-sizing: border-box;

}

.entire-content {
	margin: auto;
	width: 190px;
	-webkit-perspective: 1000px;
	        perspective: 1000px;
	position: relative;
	padding-top: 80px;
}

.content-carrousel {
	width: 100%;
	position: absolute;
	float: right;
	-webkit-animation: rotar 15s infinite linear;
	        animation: rotar 15s infinite linear;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
}

.content-carrousel:hover {
	-webkit-animation-play-state: paused;
	        animation-play-state: paused;
	cursor: pointer;
}

.content-carrousel figure {
	width: 100%;
	height: 180px;
	border: 1px solid #3b444b;
	overflow: hidden;
	position: absolute;
}

.content-carrousel figure:nth-child(1) {
	-webkit-transform: rotateY(0deg) translateZ(300px);
	        transform: rotateY(0deg) translateZ(300px); 
} .content-carrousel figure:nth-child(2) {
	-webkit-transform: rotateY(40deg) translateZ(300px);
	        transform: rotateY(40deg) translateZ(300px); 
} .content-carrousel figure:nth-child(3) {
	-webkit-transform: rotateY(80deg) translateZ(300px);
	        transform: rotateY(80deg) translateZ(300px); 
} .content-carrousel figure:nth-child(4) {
	-webkit-transform: rotateY(120deg) translateZ(300px);
	        transform: rotateY(120deg) translateZ(300px); 
} .content-carrousel figure:nth-child(5) {
	-webkit-transform: rotateY(160deg) translateZ(300px);
	        transform: rotateY(160deg) translateZ(300px); 
} .content-carrousel figure:nth-child(6) {
	-webkit-transform: rotateY(200deg) translateZ(300px);
	        transform: rotateY(200deg) translateZ(300px); 
} .content-carrousel figure:nth-child(7) {
	-webkit-transform: rotateY(240deg) translateZ(300px);
	        transform: rotateY(240deg) translateZ(300px); 
} .content-carrousel figure:nth-child(8) {
	-webkit-transform: rotateY(280deg) translateZ(300px);
	        transform: rotateY(280deg) translateZ(300px); 
} .content-carrousel figure:nth-child(9) {
	-webkit-transform: rotateY(320deg) translateZ(300px);
	        transform: rotateY(320deg) translateZ(300px); 
} .content-carrousel figure:nth-child(10) {
	-webkit-transform: rotateY(360deg) translateZ(300px);
	        transform: rotateY(360deg) translateZ(300px); 
} 

.shadow {
    position: absolute;
    box-shadow: 0px 0px 20px 0px #000;
    border-radius: 1px;
}

.content-carrousel img {
	image-rendering: auto;
	transition: all 300ms;
	width: 100%;
	height: 100%;
}

.content-carrousel img:hover {
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
	transition: all 300ms;
}

@-webkit-keyframes rotar {
	from {
		-webkit-transform: rotateY(0deg);
		        transform: rotateY(0deg);
	} to {
		-webkit-transform: rotateY(360deg);
		        transform: rotateY(360deg);
	}
}

@keyframes rotar {
	from {
		-webkit-transform: rotateY(0deg);
		        transform: rotateY(0deg);
	} to {
		-webkit-transform: rotateY(360deg);
		        transform: rotateY(360deg);
	}
}


